.div-aux-info {
  background-color: white;
  font-family: "Roboto", sans-serif;
}

.tittle-aux {
  font-family: "Roboto", sans-serif;
  font-size: 30px;
  font-weight: 700;
  color: rgb(110, 110, 110);
}

.div-main-info {
  background-color: white;
  font-family: "Roboto", sans-serif;
}

.tittle-aux-subs {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: 700;
  color: rgb(110, 110, 110);
}

.div-container {
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  margin-top: 30px;
}

.div-charts {
  display: flex;
}

.p-text {
  font-size: 20px;
  font-style: italic;
}

.p-text-div {
  max-width: 70%;
  padding-top: 100px;
  padding: 2px 0px 0px 10px;
}

.main-section {
  padding: 2px 0px 0px 2px;
  max-width: 100%;
}

.p-text-description {
  font-size: 15px;
  font-style: italic;
  align-self: flex-start;
}
